<template>
<div class="app-page evaluate-list">
  <page-content class="page-content">
    <div class="item_box">
      <img src="/static/images/personal/yz1.png" alt="">
      <div class="mid_content">
        <p class="title">新鲜紫茄子 绿色食品</p>
        <p class="m_title">规格:5kg</p>
        <div class="price_tag">
          <span>￥ 27.80</span>
          <p>x2</p>
        </div>
        <span class="refund_tag" v-if="hasRefund==1">退款中</span>
      </div>
    </div>
    <div class="title_line"><span></span>退款信息</div>
    <div class="info_box">
      <p><span>退款原因：</span>质量问题</p>
      <p><span>退款说明：</span>质量问题</p>
      <p><span>退款时间：</span>2019-01-14</p>
      <p><span>退款编号：</span>154263264562546</p>
    </div>
    <div class="title_line"><span></span>凭证图片</div>
    <div class="img_box">
      <div class="img_content"><img src="/static/images/personal/yz1.png" alt="" style="object-fit: cover;"></div>
    </div>
  </page-content>
</div>
</template>
<script>
import PageContent from '@/components/page/content'
export default {
  name: 'refunddetail',
  components: { PageContent },
  data () {
    return {
      hasRefund: 1

    }
  },
  methods: {
  },
  created () {
  }
}

</script>

<style rel='stylesheet/scss' lang='scss' scoped>
@import '@/styles/variables.scss';
.evaluate-list{
  font-family: $font-family-default!important;
  .page-content{
    position: relative;
    .title_line{
      padding: 26px 30px;
      display: flex;
      flex-direction: row;
      align-items: center;
      font-size: 24px;
      font-weight: 500;
      span{
        display: block;
        width: 10px;
        height: 28px;
        background-color: #22ac38;
        margin-right: 10px;
      }
    }
    .item_box{
      background-color: #fafafa;
      padding: 30px 32px;
      display: flex;
      flex-direction: row;
      align-items: center;
      img{
        width: 150px;
        height: 150px;
      }
      .mid_content{
        font-size: 30px;
        margin-left: 30px;
        flex: 1;
        position: relative;
        .refund_tag{
          position: absolute;
          right: 0;
          top: 10px;
          font-size: 26px;
          color: #22ac38;
        }
        .title{
          font-weight: 600;
        }
        .m_title{
          margin: 30px 0;
          font-size: 20px;
          color: #999;
        }
        .price_tag{
          display: flex;
          flex-direction: row;
          align-items: center;
          span{
            color: #c8251d;
            flex: 1;
          }
          p{
            color: #666;
          }
        }
      }
    }
    .info_box{
      padding: 15px 30px;
      border-bottom: 20px solid #fafafa;
      p{
        margin-bottom: 20px;
        font-size: 24px;
        color: #666;
      }
    }
    .img_box{
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      padding: 20px 66px;
      .img_content{
        width: 160px;
        height: 160px;
        img{
          width: 100%;
          height: 100%;
        }
      }
    }
  }

}
</style>



